@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
* {
  margin: 0;
  font-family: "Quicksand";
}

body {
  
  height: 100vh;
  background: white;
}



.TodoCounter {
    font-size: 24px;
    text-align: center;
    margin: 0;
    padding: 48px;
}
.TodoItem {
    background-color: #FAFAFA;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
  }
  
  .TodoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
  .TodoItem-p--complete {
    text-decoration: line-through;
  }
  

.Icon-container {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
}

.Icon-container--check {
    position: absolute;
    left: 12px;
}

.Icon-container--delete {
    position: absolute;
    top: -24px;
    right: 0;
}

.Icon-svg {
    width: 24px;
    height: 24px;
    transition: fill .15s;
}
.Icon-container:hover .Icon-svg--check {
    fill: #4caf50;
}
.Icon-container:hover .Icon-svg--delete {
    fill: Red;
}
lu {
    margin: 0;
    padding: 0 0 56px 0;
    list-style: none;
}
.TodoSearch {
    background: white;
    border-radius: 2px;
    border: 2px solid #202329;
    margin: 0 24px;
    height: 34px;
    width: calc(100% - 92px);
    font: 26px;
    text-align: center;
    font-weight: 600;
    color: #1E1E1F;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
}

.TodoSearch::-webkit-input-placeholder {
    color: rgb(34, 34, 34);
    font-weight: 900;
}

.TodoSearch:-ms-input-placeholder {
    color: rgb(34, 34, 34);
    font-weight: 900;
}

.TodoSearch::placeholder {
    color: rgb(34, 34, 34);
    font-weight: 900;
}

.TodoSearch:focus {
    border: 2.5px solid #202329;
}
.center {
    display: flex;
    justify-content: center;
    align-items: center;
}

.CreateTodoButton {
    text-decoration: none;
    border: 3px solid whitesmoke;
    color: transparent;
    padding: 20px 160px;
    font-size: 22px;
    letter-spacing: 5px;
    transition: all 0.5s;
    position: relative;
    margin-top: 15px;
    cursor: pointer;
    z-index: 1;
  }

  .CreateTodoButton:before {
    content: "Create a new task!";
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    background: rgb(34, 34, 34);
    font-size: 22px;
    top: 0;
    left: 100%;
    letter-spacing: 5px;
    transition: all 1s;
    height: 100%;
    width: 100%;
    position: absolute;
    -webkit-transform: scale(0) rotatey(0);
            transform: scale(0) rotatey(0);
    opacity: 0;
  }

  .CreateTodoButton:hover:before {
    -webkit-transform: scale(1) rotatey(360deg);
            transform: scale(1) rotatey(360deg);
    left: 0;
    opacity: 1;
  }
  
  .CreateTodoButton:after {
    content: "Create a new task!";
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    background: rgb(34, 34, 34);
    font-size: 22px;
    top: 0;
    left: 0;
    letter-spacing: 5px;
    transition: all 1s;
    height: 100%;
    width: 100%;
    position: absolute;
    -webkit-transform: scale(1) rotatey(0);
            transform: scale(1) rotatey(0);
    opacity: 1;
  }

  .CreateTodoButton:hover:after {
    -webkit-transform: scale(0) rotatey(360deg);
            transform: scale(0) rotatey(360deg);
    left: -100%;
    opacity: 0;
  }
  
.ModalBackground {
    color: white;
    background: rgba(32,35,41,.8);
    position: fixed;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
.LoadingTodo-container {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
}

.LoadingTodo-text {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}

.LoadingTodo-completeIcon,
.LoadingTodo-deleteIcon {
    border-radius: 50px;
    height: 48px;
    width: 48px;
}

.LoadingTodo-completeIcon {
    position: absolute;
    left: 12px;
}

.LoadingTodo-deleteIcon {
    position: absolute;
    left: -24px;
    right: 0;
}

.LoadingTodo-container,
.LoadingTodo-completeIcon,
.LoadingTodo-deleteIcon {
    background: linear-gradient(90deg, rgba(250, 250, 250, 1), rgb(200, 199, 199));
    background-size: 400% 400%;
    -webkit-animation: loadingAnimation 3s ease-in-out infinite;
            animation: loadingAnimation 3s ease-in-out infinite;
}

@-webkit-keyframes loadingAnimation {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

@keyframes loadingAnimation {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}
